import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'rupeesInWords'
})
export class RupeesInWordsPipe implements PipeTransform {

  a = ['', 'One ', 'Two ', 'Three ', 'Four ', 'Five ', 'Six ', 'Seven ', 'Eight ', 'Nine ',
       'Ten ', 'Eleven ', 'Twelve ', 'Thirteen ', 'Fourteen ', 'Fifteen ', 'Sixteen ', 'Seventeen ', 'Eighteen ', 'Nineteen '];
  b = ['', '', 'Twenty', 'Thirty', 'Forty', 'Fifty', 'Sixty', 'Seventy', 'Eighty', 'Ninety'];

  transform(value: any, args?: any): any {

    let decimal: string;
    let fraction: string;
    let rsInWords: string;

    if (value.toString().indexOf('.') === -1) {
      decimal = value;
      rsInWords = 'Rupees ' + this.inWords(decimal);
    } else {
      decimal = value.toString().split('.')[0];
      fraction = value.toString().split('.')[1];
      if (fraction.length == 1) {
        fraction = fraction + "0";
      }
      rsInWords = 'Rupees ' + this.inWords(decimal) + 'and ' + this.inWords(fraction) + 'Paisa';
    }
    return rsInWords;
  }

  inWords(num: string) {

    if (Number(num) === 0) { return 'Zero '; }

    if (num.length > 9) { return 'overflow'; }

    const n = ('000000000' + num).substr(- 9).match(/^(\d{2})(\d{2})(\d{2})(\d{1})(\d{2})$/);
    if (!n) { return; }

    let str = '';
    str += (Number(n[1]) !== 0) ? (this.a[Number(n[1])] || this.b[n[1][0]] + ' ' + this.a[n[1][1]]) + 'Crore ' : '';
    str += (Number(n[2]) !== 0) ? (this.a[Number(n[2])] || this.b[n[2][0]] + ' ' + this.a[n[2][1]]) + 'Lakh ' : '';
    str += (Number(n[3]) !== 0) ? (this.a[Number(n[3])] || this.b[n[3][0]] + ' ' + this.a[n[3][1]]) + 'Thousand ' : '';
    str += (Number(n[4]) !== 0) ? (this.a[Number(n[4])] || this.b[n[4][0]] + ' ' + this.a[n[4][1]]) + 'Hundred ' : '';
    str += (Number(n[5]) !== 0) ? ((str !== '') ? '' : '') +
    (this.a[Number(n[5])] || this.b[n[5][0]] + ' ' + this.a[n[5][1]]) + ' ' : '';
    return str;
  }
}
